export default {
	data() {
		return {
			showadd: true,
			uname: '',
			tel: '',
			time: '', // 录入时间
			address: '', // 地址
			// key: '',
			longitude: '', //经度
			latitude: '', // 纬度
			imgsrc: ''
		}
	},
	methods: {
		onShow() {
			// 地址信息
			uni.getLocation({
				type: 'wgs84',
				geocode: 'true',
				success: function(res) {
					console.log(res, 'res')
					console.log('当前位置的经度：' + res.longitude);
					console.log('当前位置的纬度：' + res.latitude);
					this.longitude = res.longitude;
					this.latitude = res.latitude;
				}
			})
		},
		addclick() {
			// 上传图片
			let that = this
			uni.chooseImage({
				count: 6, //默认9
				sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function(res) {
					uni.uploadFile({
						url: 'http://weilang.fryxy.cn/api/HttpImager.ashx?Function=HttpCreatePicture', //仅为示例，非真实的接口地址
						filePath: res.tempFilePaths[0],
						name: 'file',
						formData: {
							'user': 'test',
							'ClientAddr': '218.109.203.404921'
						},
						success: (result) => {
							console.log(result)
							that.imgsrc = JSON.parse(result.data).Body
							that.showadd = false;
						}
					});
				}
			})
		},
		onReady() {
			// 录入时间
			let now = new Date();
			var y = now.getFullYear();
			var m = now.getMonth() + 1;
			m = m < 10 ? ('0' + m) : m;
			var d = now.getDate();
			d = d < 10 ? ('0' + d) : d;
			var h = now.getHours();
			h = h < 10 ? ('0' + h) : h;
			var minute = now.getMinutes();
			var second = now.getSeconds();
			this.time = y + '-' + m + '-' + d + ' ' + h + '-' + minute + '-' + second;
		}
	}
}
